import React, {useEffect, useState} from 'react';
import contents from './articles-content';
import ArticlesList from '../components/ArticlesList';
import UpVotesSection from '../components/upVotesSection';
import AddComment from '../components/AddComment';
import NotFound from './NotFound';
import CommentsList from "../components/CommentsList";


const Article = ({match})=> {
    const name = match.params.name;
    const article = contents.find(article => article.name === name);


    const [articleInfo, setArticleInfo] = useState({upVotes:0 , comments : []});

    useEffect(()=>{
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            setArticleInfo(body);
        }
        fetchData();
    },[name]);

    if(!article)return <NotFound />;

    const otherArticles = contents.filter(article => article.name !== name );

    return (
        <>
            <h1>{article.title}</h1>
            <UpVotesSection articleName={name} upVotes={articleInfo.upVotes} setArticleInfo={setArticleInfo} />
            {article.content.map((para, key) => (
                <p key={key}>{para}</p>        
            ))}
            <CommentsList comments={articleInfo.comments} />
            <AddComment articleName={name} setArticleInfo={setArticleInfo} />
            <h3>Other Articles</h3>
            <ArticlesList articles={otherArticles} />
            
        </>
    );
}

export default Article;